
import {useContext} from "react";
import Editor from "./Editor";
import {Box,styled}  from "@mui/material";
import { DataContext } from "../context/DataProvider";

const Container = styled(Box)`
    display: flex;
    background-color : #F0F8FF;
    height: 62vh;
`
const Code =  () => {

      const {html , setHtml , css , setCss, js , setjs} = useContext(DataContext);
    return (
        <Container>
           <Editor
           heading = "HTML"
           icons = "/"
           color = "#FF3C41"
           value ={html}
           onChange = {setHtml}
        />
        <Editor
          heading = "CSS"
          icons = "*"
          color = "#0EBEFF"
          value ={css}
          onChange = {setCss}
        />
        <Editor
          heading = "JAVASCRIPT"
          icons = "()"
          color = "#FCD000"
          value ={js}
          onChange = {setjs}
        
        />

        
        </Container>
    )
    
}

export default Code;


