import {AppBar , Toolbar , styled } from '@mui/material';

const Container = styled(AppBar)`
      background: #F0F8FF;
      height = 1vh;
`

const Header = () => {

    const logo = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRtW4Qe0z80X3c_gpYY2kYm0GImakTdcX94LH8OMR0xeSniQc1X0QwR2MTSU_k3FRxNhwI&usqp=CAU'

    return (
       
        <Container position = 'static'>
            <Toolbar>
                <img src= {logo} alt = "logo" style ={{width:100}} />

            </Toolbar>
        </Container>


    )
}

export default Header;